body {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
    background: url(/static/media/midnight.a5b64575.gif) no-repeat center center fixed;
    background-size: cover;
    display: grid;
    justify-content: center;
    align-items: center;
    /* height: 700px;
    width: 1000px; */
}

#background {
    margin-top: 200px;
}

.group {
    justify-content: center;
    align-items: center;
    font-family: 'Cormorant Infant', serif;
    font-size: 3.5em;
    text-align: center;
}

.input-view {
    height: 35px;
    width: 225px;
    font-family: 'Cormorant Infant', serif;
    font-size: .55em;
    text-align: center;
}

.button-view {
    font-family: 'Cormorant Infant', serif;
    font-size: .5em;
    text-align: center;
    cursor: pointer;
    background-color: rgb(40, 124, 192);
    color: yellow;
    border-radius: 5px;
}

@media (max-width: 800px) {

    html {
        background: black;
    }

    #background {
        background: url(/static/media/midnight.a5b64575.gif) no-repeat center center fixed;
        background-size: cover;
        display: grid;
        justify-content: center;
        align-items: center;
        height: 400px;
        width: 700px;
        margin-top: 0px;
    }

    .group {
        justify-content: center;
        align-items: center;
        font-family: 'Cormorant Infant', serif;
        font-size: 2em;
        text-align: center;
    }
    
    .input-view {
        height: 20px;
        width: 125px;
        font-family: 'Cormorant Infant', serif;
        font-size: .40em;
        text-align: center;
    }
    
    .button-view {
        font-family: 'Cormorant Infant', serif;
        font-size: .4em;
        text-align: center;
        cursor: pointer;
        background-color: rgb(40, 124, 192);
        color: yellow;
        border-radius: 5px;
    }
}

@media (max-width: 500px) {
    #background {
        height: 700px;
        width: 380px;
    }
}
#container-left {
  position: relative;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  padding: 0;
  list-style: none;
  margin: 1em auto 0; }
  #container-left > * {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -10px;
    width: 20px;
    height: 20px; }
    #container-left > *:nth-of-type(1) {
      -webkit-transform: rotate(0deg) translate(150px) rotate(0deg);
              transform: rotate(0deg) translate(150px) rotate(0deg); }
    #container-left > *:nth-of-type(2) {
      -webkit-transform: rotate(30deg) translate(150px) rotate(-30deg);
              transform: rotate(30deg) translate(150px) rotate(-30deg); }
    #container-left > *:nth-of-type(3) {
      -webkit-transform: rotate(60deg) translate(150px) rotate(-60deg);
              transform: rotate(60deg) translate(150px) rotate(-60deg); }
    #container-left > *:nth-of-type(4) {
      -webkit-transform: rotate(90deg) translate(150px) rotate(-90deg);
              transform: rotate(90deg) translate(150px) rotate(-90deg); }
    #container-left > *:nth-of-type(5) {
      -webkit-transform: rotate(120deg) translate(150px) rotate(-120deg);
              transform: rotate(120deg) translate(150px) rotate(-120deg); }
    #container-left > *:nth-of-type(6) {
      -webkit-transform: rotate(150deg) translate(150px) rotate(-150deg);
              transform: rotate(150deg) translate(150px) rotate(-150deg); }
    #container-left > *:nth-of-type(7) {
      -webkit-transform: rotate(180deg) translate(150px) rotate(-180deg);
              transform: rotate(180deg) translate(150px) rotate(-180deg); }
    #container-left > *:nth-of-type(8) {
      -webkit-transform: rotate(210deg) translate(150px) rotate(-210deg);
              transform: rotate(210deg) translate(150px) rotate(-210deg); }
    #container-left > *:nth-of-type(9) {
      -webkit-transform: rotate(240deg) translate(150px) rotate(-240deg);
              transform: rotate(240deg) translate(150px) rotate(-240deg); }
    #container-left > *:nth-of-type(10) {
      -webkit-transform: rotate(270deg) translate(150px) rotate(-270deg);
              transform: rotate(270deg) translate(150px) rotate(-270deg); }
    #container-left > *:nth-of-type(11) {
      -webkit-transform: rotate(300deg) translate(150px) rotate(-300deg);
              transform: rotate(300deg) translate(150px) rotate(-300deg); }
    #container-left > *:nth-of-type(12) {
      -webkit-transform: rotate(330deg) translate(150px) rotate(-330deg);
              transform: rotate(330deg) translate(150px) rotate(-330deg); }
  #container-left div {
    height: 10px;
    width: 20px;
    display: block;
    max-width: 100%;
    border-radius: 15%;
    -webkit-transition: .15s;
    transition: .15s; }

