@import url('https://fonts.googleapis.com/css?family=Pacifico&display=swap');

#operatorView {
    width: 1920px;
    height: 1298px;
    background-image: url(./glasses.png);
}

.change-button {
    cursor: pointer;
    background-color: rgb(40, 124, 192);
    color: yellow;
    border-radius: 5px;
    font-size: 1.3em;
    margin-top: 50px;
    margin-left: 50px;
}

#title {
    font-family: 'Pacifico', cursive;
    color: yellow;
    display: flex;
    justify-content: center;
    margin-top: 50px;
    font-size: 3em;
}

.flex {
    display: flex;
    justify-content: center;
}

#sessionLeft {
    margin:10px;
}

#sessionRight {
    margin:10px;
}