@import url('https://fonts.googleapis.com/css?family=Cormorant+Infant:700&display=swap');

html {
    background: url(./midnight.gif) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    display: grid;
    justify-content: center;
    align-items: center;
    /* height: 700px;
    width: 1000px; */
}

#background {
    margin-top: 200px;
}

.group {
    justify-content: center;
    align-items: center;
    font-family: 'Cormorant Infant', serif;
    font-size: 3.5em;
    text-align: center;
}

.input-view {
    height: 35px;
    width: 225px;
    font-family: 'Cormorant Infant', serif;
    font-size: .55em;
    text-align: center;
}

.button-view {
    font-family: 'Cormorant Infant', serif;
    font-size: .5em;
    text-align: center;
    cursor: pointer;
    background-color: rgb(40, 124, 192);
    color: yellow;
    border-radius: 5px;
}

@media (max-width: 800px) {

    html {
        background: black;
    }

    #background {
        background: url(./midnight.gif) no-repeat center center fixed; 
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: cover;
        display: grid;
        justify-content: center;
        align-items: center;
        height: 400px;
        width: 700px;
        margin-top: 0px;
    }

    .group {
        justify-content: center;
        align-items: center;
        font-family: 'Cormorant Infant', serif;
        font-size: 2em;
        text-align: center;
    }
    
    .input-view {
        height: 20px;
        width: 125px;
        font-family: 'Cormorant Infant', serif;
        font-size: .40em;
        text-align: center;
    }
    
    .button-view {
        font-family: 'Cormorant Infant', serif;
        font-size: .4em;
        text-align: center;
        cursor: pointer;
        background-color: rgb(40, 124, 192);
        color: yellow;
        border-radius: 5px;
    }
}

@media (max-width: 500px) {
    #background {
        height: 700px;
        width: 380px;
    }
}